.heading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 0 50px 0;
}
.screen-sub-heading {
  letter-spacing: 3px;
  margin: 8px 0 10px 0;
  font-size: 12px;
  color: #082ccf;
}
.screen-heading {
  font-size: 35px;
  color: #082ccf;
  font-family: "Didot";
}
.heading-seperator {
  display: flex;
  align-items: center;
  position: relative;
  width: 180px;
  margin: 10px 0 0 0;
}
.seperator-line {
  width: 100%;
  height: 2px;
  background-color: #082ccf;
}
